import GoodList from '@/components/Good/GoodList'
import FruitSingle from '@/components/Good/FruitSingle'

export default {
  path: '/good',
  component: GoodList,
  children: [
    {
      path: '',
      name: 'GoodIndex',
      redirect: {
        name: 'FruitSingle'
      }
    },
    {
      path: 'single',
      name: 'FruitSingle',
      component: FruitSingle
    }
  ]
}



