import Vue from 'vue'
import Router from 'vue-router'

import GoodList from './Good/GoodList'
import FruitDetail from './Good/FruitDetail'

Vue.use(Router)

export default new Router({
  mode: 'history', // 可以去掉url中的"#/"
  routes: [
    {
      path: '/',
      name: 'Index',
      redirect: {
        name: 'FruitSingle'
      }
    },
    GoodList,
    FruitDetail
  ]
})



